.item{border:1px solid #e0e0e0}.item__imageContainer{overflow:hidden}.item__image{transition:all 0.3s}.item__image:hover{transform:scale(1.05)}.item__link:hover{text-decoration:none}.item__name{text-align:center;color:#000;text-decoration:none;font-size:1.5rem}.item__price{display:flex;justify-content:center;font-size:1.5rem;font-weight:500}.item__price--latestPrice{color:red}.item__price--initialPrice{color:gray;text-decoration:line-through}.item__contact{text-align:center;font-size:1.5rem;color:red;font-weight:500}.device{margin:0 5px}

