.sidebar{width:15%;flex-shrink:0}.sidebar-header{padding:1rem;background:#000;color:#fff;font-size:1.5rem;border-radius:5px;margin-bottom:10px}.sidebar-main .item{margin-bottom:2rem}.sidebar-main .item:first-child{margin-top:2rem}.sidebar-item__name{margin:1rem 0;color:#000;text-align:center;font-size:1.5rem}@media (max-width: 1024px){.sidebar{display:none}}

.home-main{display:flex}.home-main .productLists{flex:1 1}

.imageZoom_root__3RcdJ {
    margin: 0 auto;
}

.imageZoom_zoomContainer__30VW3 {
    width: 400px;
    height: 400px;
    overflow: hidden;
    position: absolute;
    right: 0;
    top: 0;
    z-index: 10;
    border: 1px solid #d4d4d4;
    box-shadow: 3px 3px 20px rgba(0, 0, 0, 0.25);
}

.imageZoom_root__3RcdJ img,
.imageZoom_root__3RcdJ > div {
    cursor: crosshair;
}

.imageZoom_mainImage__9yNgN {
    width: 450px;
}

.imageZoom_mainImage__9yNgN img {
    width: 100%;
}

@media print {
    .imageZoom_root__3RcdJ {
        width: 250px !important;
        height: 250px !important;
    }
    .imageZoom_root__3RcdJ > div {
        height: 250px !important;
    }
}

@media (min-width: 1025px) and (max-width: 1800px) {
    .imageZoom_zoomContainer__30VW3 {
        width: 350px;
        height: 300px;
        right: 25px;
    }
}

@media (max-width: 1200px) {
    .imageZoom_zoomContainer__30VW3 {
        display: none;
    }

    .imageZoom_root__3RcdJ {
        margin: 0 auto;
        width: 100% !important;
    }
}

@media (max-width: 1024px) {
    .imageZoom_mainImage__9yNgN {
        max-width: 100%;
    }
}

@media (max-width: 500px) {
    .imageZoom_root__3RcdJ {
        max-width: 95vw;
    }
}

